import styled from "styled-components";

export default styled.div`

  width: 100%;
  height: 10vh;
  background:'white';
  padding: 0 5rem;
  font-size:1.2em;
  align-content: center;
  align-items: center;
  flex: wrap;

    @media (max-width: 670px) {
      padding: 0;
}
  `;



