import styled from "styled-components";

export default styled.div`
font-size:1.3rem;
text-align:justify;
margin: 4rem 0;
h1,h2,h3{
  text-align:center;
  margin-bottom:1rem;
}
h3{
  margin-bottom:2rem;

}
    @media (max-width: 670px) {
      text-align:center;

    }
  `;



